import React from 'react'
import { Button, Dialog, Table } from '@alifd/next'
import { get, post } from '../utils/api'
const curdir = window.RMX.project.dir

class Models extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      models: [],
      repeatApis: [],
      originModels: [],
      noMatchAPis: [],
      canClick: false,
      errorTip: ''
    }
  }
  componentDidMount () {
    get('/extra/magix/config', {
      cwd: curdir
    }).then(res => {
      if (res && res.rapProjectId) {
        this.checkModels()
      } else {
        this.setState({
          errorTip: 'rapProjectId'
        })
      }
    })
  }

  checkModels () {
    post('/extra/magix/check/models', {
      cwd: curdir
    }).then(res => {
      if (res && res.data) {
        const canClick = res.data.noMatchAPis && res.data.noMatchAPis.length > 0
        this.setState({
          models: res.data.models || [],
          repeatApis: res.data.repeatApis || [],
          originModels: res.data.originModels || [],
          noMatchAPis: res.data.noMatchAPis || [],
          canClick: true,
          errorTip: canClick ? '' : 'RAP'
        })
      }
    })
  }
  exec () {
    const {
      models,
      repeatApis,
      originModels
    } = this.state
    this.props.emit('models', 'magixModels', {
      cwd: curdir,
      models,
      repeatApis,
      originModels
    })
  }

  handleCommand = () => {
    const {
      noMatchAPis
    } = this.state
    const me = this
    if (noMatchAPis.length > 0) {
      const dailog = Dialog.confirm({
        title: '',
        content: 'RAP',
        onOk: () => {
          dailog.hide()
          me.exec()
          me.setState({
            noMatchAPis: []
          })
        },
        onCancel: () => {
          dailog.hide()
        }
      })
    } else {
      me.exec()
    }
  }
  showNoMatch () {
    const {
      noMatchAPis
    } = this.state
    const dialog = Dialog.show({
      title: 'RAP',
      content: <Table dataSource={noMatchAPis} style={{ width: 600 }}>
        <Table.Column title='api' dataIndex='name' />
        <Table.Column title='' dataIndex='method' />
        <Table.Column title='' dataIndex='url' />
      </Table>,
      footer: <Button onClick={() => { dialog.hide() }}></Button>
    })
  }
  render () {
    // 0 
    // 1 
    const {
      status
    } = this.props
    const {
      noMatchAPis,
      canClick,
      errorTip
    } = this.state
    return (
      <div className='task-detail'>
        {noMatchAPis.length > 0
          ? <div className='warning-list'>
            <div className='warning-link' onClick={() => { this.showNoMatch() }}>RAP</div>
          </div>
          : null
        }
        <div className='action-head'>RAP</div>
        <div className='action-btn'>
          <Button disabled={!canClick || (status == 1)} onClick={() => this.handleCommand()} ></Button>
          {/* <span className="action-error">{errorTip}</span> */}
        </div>

      </div>
    )
  }
}

export default Models
