'use strict'
import React from 'react'
import { Button, Input } from '@alifd/next'
import { get, post } from '../utils/api'
const curdir = window.RMX.project.dir

class Iconfont extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      canClick: false,
      errorTip: ''
    }
  }
  componentDidMount () {
    get('/extra/magix/config', {
      cwd: curdir
    }).then(res => {
      if (res) {
        let errorTip = ''
        if (!res.iconfontId) {
          errorTip = 'iconfontId'
        } else if (!res.iconfontPath) {
          errorTip = 'iconfontPath'
        }
        this.setState({
          canClick: res.iconfontId && res.iconfontPath,
          errorTip: errorTip
        })
      }
    })
  }
  exec () {
    this.props.emit('iconfont', 'magixIconfont', {
      cwd: curdir
    })
  }
  check () {
    this.props.emit('iconfont', 'magixCheckIconfont', {
      cwd: curdir
    })
  }
  render () {
    // 0 
    // 1 
    const {
      status
    } = this.props
    const {
      canClick,
      errorTip
    } = this.state
    if (status == 1) {
      this.hasCheckOk = true
    }
    return <div className='task-detail'>
      <div className='action-head'>iconfont</div>
      <div className='action-btn'>
        <Button className='_mr10' disabled={!canClick || (status == 1)} onClick={() => this.check()} ></Button>
        <Button disabled={!canClick || status == 1 || !this.hasCheckOk} onClick={() => this.exec()} ></Button>
        <span className='action-error'>{errorTip}</span>
      </div>
    </div>
  }
}

export default Iconfont
